export enum FieldType {
  STRING = 1,
  NUMBER,
  OBJECT,
  STRING_ARRAY,
  NUMBER_ARRAY,
  DATE,
}
/**
 * Window   innerWidth, innerHeight
 */
export type Viewport = {
  width: number;
  height: number;
};

/**
 *   (rect viewport, position   )
 */
export type Position = {
  top: number;
  left: number;
};

export type MovePositionOption = {
  topMargin?: number;
  leftMargin?: number;
};
