import {
  FC,
  createContext,
  useContext,
  useEffect,
  useMemo,
  useState,
} from "react";

import styled from "@emotion/styled";

import {
  BUTTON_DOMAIN_ID,
  Domain,
  DomainId,
  getDomain,
  hasChildren,
} from "./utils/domain";

import "./package/style.css";
import "./style.css";

import Playground, { PlaygroundComponent, registry } from "./Playground";

import { ToastProvider } from "./package/components/feedback/Toast";

const DomainContext = createContext<{
  handleDomainClick: (domain: Domain) => void;
}>({
  handleDomainClick: () => {},
});

const App: FC<{ domains: Domain[] }> = ({ domains }) => {
  const [domain, setDomain] = useState<Domain | undefined>(
    getDomain(BUTTON_DOMAIN_ID, domains)
  );
  const playgroundObject = useMemo(() => {
    if (domain) {
      const domainId = domain.id as DomainId;
      if (registry[domainId]) {
        return registry[domainId];
      }
    }

    return null;
  }, [domain]);

  const [playgroundComponent, setPlaygroundComponent] =
    useState<PlaygroundComponent>();
  useEffect(() => {
    if (playgroundObject) {
      setPlaygroundComponent(playgroundObject.components[0]);
    }
  }, [playgroundObject]);

  const handleDomainClick = (domain: Domain) => {
    if (!hasChildren(domain)) {
      const componentDomain = getDomain(domain.id, domains);
      setDomain(componentDomain);
    }
  };

  const handlePlaygroundComponentClick = (
    playgroundComponent: PlaygroundComponent
  ) => {
    setPlaygroundComponent(playgroundComponent);
  };

  return (
    <DomainContext.Provider
      value={{
        handleDomainClick,
      }}
    >
      <ToastProvider>
        <Container>
          <Nav>
            {<DomainList domains={domains} />}
            <CaseList>
              {playgroundObject?.components.map(
                (playgroundComponent, index) => {
                  const { title } = playgroundComponent;

                  return (
                    <li
                      key={index}
                      onClick={() =>
                        handlePlaygroundComponentClick(playgroundComponent)
                      }
                    >
                      {title}
                    </li>
                  );
                }
              )}
            </CaseList>
          </Nav>

          {playgroundComponent && (
            <Playground
              key={playgroundComponent.title}
              component={playgroundComponent}
              controls={
                playgroundObject?.controls || playgroundComponent.controls
              }
            />
          )}
        </Container>
      </ToastProvider>
    </DomainContext.Provider>
  );
};

export default App;

const DomainList: FC<{
  domains: Domain[];
}> = ({ domains }) => {
  const { handleDomainClick } = useContext(DomainContext);

  return (
    <ul>
      {domains.map((domain) => {
        return (
          <li key={domain.id}>
            <div onClick={() => handleDomainClick(domain)}>{domain.title}</div>
            {hasChildren(domain) && <DomainList domains={domain.children} />}
          </li>
        );
      })}
    </ul>
  );
};

const Container = styled.div`
  display: flex;
`;

const Nav = styled.nav`
  display: flex;
`;

const CaseList = styled.ul``;
