import { NumberUtility } from "../../../core/util";
import {
  codeDataRegionDepth1,
  codeDataRegionDepth2,
  codeDataRegionDepth3,
} from "./data";
import { CodeData } from "./types";

const { getRandomNumber } = NumberUtility;

/*
 zoom / 
 8 / 30km
 9 / 20km
 10 / 10km
 11 / 5km
 12 / 3km
 13 / 1km
 14 / 500m
 15 / 300m
 16 / 100m
 17 / 50m
 18 / 30m
 19 / 20m
 20 / 10m
 21 / 5m
 */

/*
### SaleType.CODE
-  1km (zoom 13 )  
-  500m ~ 101m (zoom 14 ~ 15)  
-  100m (zoom 16)  
*/

export const getCodeData = async (map: naver.maps.Map) => {
  return new Promise<CodeData[]>((resolve) => {
    setTimeout(() => {
      const zoom = map.getZoom();

      if (zoom <= 13) {
        resolve(codeDataRegionDepth1);
      } else if (14 <= zoom && zoom <= 15) {
        resolve(codeDataRegionDepth2);
      } else if (16 <= zoom) {
        resolve(codeDataRegionDepth3);
      } else {
        resolve([]);
      }
    }, 600);
  });
};

export const registScript = async (ncpClientId: string) => {
  return new Promise<boolean>((resolve) => {
    const id = `naver-map-script-${ncpClientId}`;
    const mapScript = document.createElement("script");
    mapScript.id = id;
    mapScript.src = `https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=${ncpClientId}&submodules=geocoder`;
    mapScript.onload = () => {
      if (typeof window.naver !== "undefined") {
        return resolve(true);
      }
    };

    if (typeof window.naver === "undefined" && !document.getElementById(id)) {
      document.body.appendChild(mapScript);

      setTimeout(() => {
        resolve(true);
      }, 600);
    } else {
      resolve(true);
    }
  });
};

export const getRandomCoordinates = (
  bounds: naver.maps.Bounds,
  length: number = 10
): [number, number][] => {
  const min = bounds.getMin();
  const max = bounds.getMax();

  const minX = min.x;
  const minY = min.y;
  const maxX = max.x;
  const maxY = max.y;

  return Array.from({ length }).map(() => [
    getRandomNumber(minY, maxY, 8),
    getRandomNumber(minX, maxX, 8),
  ]);
};
