import { useCallback, useEffect, useState } from "react";
import { v4 } from "uuid";

import { DialogComponent } from "./types";
import { DIALOG_DATA_ATTRIBUTE_NAME, DialogContext } from "./context";

import Body from "./Body";
import Trigger from "./Trigger";

import { Position } from "../util";

const Dialog: DialogComponent = ({ children }) => {
  const [id, setId] = useState<string>();

  useEffect(() => {
    setId(v4());
  }, []);

  const [isOpen, setIsOpen] = useState<boolean>(false);

  // Trigger viewport  
  const [triggerRect, setTriggerRect] = useState<DOMRect>();

  // Trigger   
  const [triggerPosition, setTriggerPosition] = useState<Position>({
    top: 0,
    left: 0,
  });

  /*  Element bubble  ID  dialog ID 
  window click close Effect    .
  (stopPropagation  issue ) */
  const handleWindowClose = useCallback(
    (e: MouseEvent) => {
      const isTarget = e?.composedPath().some((target) => {
        const element = target as HTMLElement;
        const isElement = !!element.getAttribute;

        if (isElement) {
          return (
            (target as HTMLElement).getAttribute(DIALOG_DATA_ATTRIBUTE_NAME) ===
            id
          );
        } else {
          return false;
        }
      });

      if (!isTarget) {
        setIsOpen(false);
      }
    },
    [id]
  );

  const handleClose = () => {
    setIsOpen(false);
  };

  // open handler (  Trigger ,    .)
  const handleOpen = (e?: React.MouseEvent) => {
    const target = e?.currentTarget as HTMLElement;

    if (target) {
      setTriggerRect(target.getBoundingClientRect());
      setTriggerPosition({
        top: target.offsetTop,
        left: target.offsetLeft,
      });
    }

    setIsOpen(true);
  };

  const contextValue = {
    id,
    isOpen,
    triggerPosition,
    triggerRect,
    handleOpen,
    handleClose,
    handleWindowClose,
  };
  return (
    <DialogContext.Provider value={contextValue}>
      {children(contextValue)}
    </DialogContext.Provider>
  );
};

Dialog.Trigger = Trigger;
Dialog.Body = Body;

export default Dialog;
