import { FC } from "react";

import { CodeMarkerProps } from "./types";
import CodeMarkerStyledComponents from "./styled";

import { NumberUtility } from "../../../../core/util";

const { getKoreanUnitNumber } = NumberUtility;

const { Container, Price, RegionName } = CodeMarkerStyledComponents;

const CodeMarker: FC<CodeMarkerProps> = ({ regionName, price }) => {
  return (
    <Container>
      <RegionName>{regionName}</RegionName>
      <Price>{getKoreanUnitNumber(price)}</Price>
    </Container>
  );
};

export default CodeMarker;
