import { FC, useCallback, useEffect, useState } from "react";
import { createPortal } from "react-dom";

import { R114MapProps, SaleType } from "./types";
import R114MapStyledComponents from "./styled";

import useNaverMap from "./useNaverMap";

import { RadioButton } from "../../../components";
import { getCodeData } from "./util";
import CodeMarker from "./CodeMarker";

const {
  Container,
  SidePanel,
  MapArea,
  RadioRow,
  SidePanelToggle,
  SidePanelContents,
} = R114MapStyledComponents;

const mapOptions: naver.maps.MapOptions = {
  zoom: 12,
};

const R114Map: FC<R114MapProps> = ({ ncpClientId = "zp20eln829" }) => {
  const { ref, state, dispatch, isLoading } = useNaverMap(
    ncpClientId,
    mapOptions
  );

  const { map, markers } = state;

  const [isSidePanelOpen, setIsSidePanelOpen] = useState(true);
  const handleSidePanelTransitionEnd = useCallback(() => {
    if (map) {
      map.autoResize();
    }
  }, [map]);

  //  
  const [saleType, setSaleType] = useState<SaleType>(SaleType.CODE);

  //   
  const dataBindToMap = useCallback(
    async (saleType: SaleType) => {
      if (map) {
        switch (saleType) {
          case SaleType.CODE: {
            getCodeData(map).then((codeDatas) => {
              dispatch({ type: "clearMarker", payload: undefined });
              codeDatas.forEach((codeData) => {
                dispatch({
                  type: "setMarker",
                  payload: {
                    id: codeData.id,
                    position: new naver.maps.LatLng(codeData.coord),
                    component: (
                      <CodeMarker
                        regionName={codeData.name}
                        price={codeData.price}
                      />
                    ),
                  },
                });
              });
            });
            break;
          }
          case SaleType.NONE_CODE:
            break;
        }
      }
    },
    [dispatch, map]
  );

  //   
  useEffect(() => {
    if (map) {
      dataBindToMap(saleType);

      const idleEvent = map.addListener("idle", () => {
        dataBindToMap(saleType);
      });

      return () => {
        map.removeListener(idleEvent);
      };
    }
  }, [dataBindToMap, map, saleType]);

  useEffect(() => {
    if (map) {
      const zoomstartEvent = map.addListener("zoomstart", () => {
        dispatch({ type: "clearMarker", payload: undefined });
      });

      return () => {
        map.removeListener(zoomstartEvent);
      };
    }
  }, [dispatch, map]);

  return (
    <Container>
      {isLoading && "Loading..."}
      <MapArea ref={ref} />
      <SidePanel>
        <SidePanelToggle
          onClick={() => {
            setIsSidePanelOpen(!isSidePanelOpen);
          }}
        >
          {isSidePanelOpen ? ">" : "<"}
        </SidePanelToggle>
        <SidePanelContents
          style={{
            width: isSidePanelOpen ? 300 : 0,
          }}
          onTransitionEnd={handleSidePanelTransitionEnd}
        >
          <RadioRow>
            <RadioButton
              label=" "
              isChecked={saleType === SaleType.CODE}
              onChange={() => setSaleType(SaleType.CODE)}
            />
            <RadioButton
              label=" "
              isChecked={saleType === SaleType.NONE_CODE}
              onChange={() => setSaleType(SaleType.NONE_CODE)}
            />
          </RadioRow>
        </SidePanelContents>
      </SidePanel>
      {markers.map((marker) => {
        const container = document.getElementById(marker.id);

        if (container) {
          return createPortal(marker.component, container);
        } else {
          return null;
        }
      })}
    </Container>
  );
};

export default R114Map;
