import { useEffect, useReducer, useRef, useState } from "react";

import { MapReducerAction, MapReducerState } from "./types";

import { registScript } from "./util";

const useNaverMap = (ncpClientId: string, options?: naver.maps.MapOptions) => {
  const ref = useRef<HTMLDivElement>(null);

  const [state, dispatch] = useReducer(reducer, {
    markers: [],
  });

  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    registScript(ncpClientId).then(() => {
      if (ref.current && !state.map) {
        const instance = new naver.maps.Map(ref.current, options);
        dispatch({ type: "initMap", payload: instance });
        setIsLoading(false);

        return () => {
          instance.destroy();
        };
      }
    });
  }, [ncpClientId, options, state.map]);

  return {
    ref,
    state,
    isLoading,
    dispatch,
  };
};

const reducer = (state: MapReducerState, action: MapReducerAction) => {
  switch (action.type) {
    case "initMap": {
      state.map = action.payload;

      return { ...state };
    }
    case "setMarker": {
      if (state.map) {
        const id = action.payload.id;

        const marker = new naver.maps.Marker({
          position: action.payload.position,
          icon: {
            content: `<div id="${id}"></div>`,
          },
        });

        state.markers.push({
          id,
          marker,
          component: action.payload.component,
        });

        marker.setMap(state.map);
      }
      return { ...state };
    }
    case "clearMarker": {
      if (state.map) {
        state.markers.forEach(({ marker }) => {
          marker.setMap(null);
        });

        state.markers = [];
      }
      return state;
    }
  }
};

export default useNaverMap;
