import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import {
  COMPONENT_DOMAIN_ID,
  DATA_VISUALIZATION_DOMAIN_ID,
  OTHER_DOMAIN_ID,
  getDomain,
} from "./utils/domain";

import domainData from "./domainData.json";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);

const { domains } = domainData;

const componentDomain = getDomain(COMPONENT_DOMAIN_ID, domains.reverse());
const dataVisualizationDomain = getDomain(
  DATA_VISUALIZATION_DOMAIN_ID,
  domains.reverse()
);
const otherDomain = getDomain(OTHER_DOMAIN_ID, domains.reverse());

if (componentDomain && dataVisualizationDomain && otherDomain) {
  root.render(
    <App
      domains={
        [
          ...componentDomain.children,
          ...dataVisualizationDomain.children,
          ...otherDomain.children,
        ] || []
      }
    />
  );
}
