import { useMemo } from "react";

import { DropdownComponent, DropdownOption } from "./types";
import DropdownStyledComponents from "./styled";

import DropdownContext from "./context";

import Input from "./Input";
import List from "./List";

import Dialog from "../../../core/Dialog";

const { Container } = DropdownStyledComponents;

const Dropdown: DropdownComponent = ({
  size = "medium",
  options,
  selectedValue,
  onChange,
  isBlock,
  isDisabled,
  ...props
}) => {
  const selectedOption = useMemo(
    () => options.find(({ value }) => value === selectedValue) || options[0],
    [options, selectedValue]
  );

  const handleChange = (value: DropdownOption["value"]) => {
    if (onChange) {
      onChange(value);
    }
  };

  return (
    <DropdownContext.Provider value={{ size }}>
      <Container>
        <Dialog>
          {({ isOpen, handleClose }) => {
            return (
              <>
                <Dialog.Trigger>
                  <Input
                    isBlock={isBlock}
                    isDisabled={isDisabled}
                    isSelected={isOpen}
                    value={
                      selectedOption?.title || selectedOption.value.toString()
                    }
                    {...props}
                  />
                </Dialog.Trigger>
                <Dialog.Body>
                  <Dialog.Body.Dropdown margin={4}>
                    <List
                      options={options}
                      onItemClick={(value) => {
                        handleChange(value);
                        handleClose();
                      }}
                    />
                  </Dialog.Body.Dropdown>
                </Dialog.Body>
              </>
            );
          }}
        </Dialog>
      </Container>
    </DropdownContext.Provider>
  );
};

Dropdown.Input = Input;
Dropdown.List = List;

export default Dropdown;
